import { defineComponent, onMounted } from "vue";
import { defaultImgUrl } from "@/const/appConsts";
import router from "@/router";
import RouterNames from "@/router/name";
import AOS from "aos";
export default defineComponent({
  name: "TripListItem",
  props: {
    imgUrl: {
      type: String
    },
    trip_title: {
      type: String
    },
    trip_content: {
      type: String
    },
    trip_id: {
      type: Number,
      require: true
    }
  },

  setup(props, {
    emit
  }) {
    const goTripUrl = () => {
      router.push({
        name: RouterNames.tripPage,
        params: {
          id: props.trip_id
        }
      });
    };

    onMounted(async () => {
      AOS.init();
    });
    return {
      defaultImgUrl,
      goTripUrl
    };
  }

});